import React from 'react'
import { Field, FieldProps } from 'react-form'
import { FormFeedback, Input, InputProps } from 'reactstrap'

interface IProps
  extends Omit<FieldProps<string>, 'render' | 'children'>,
    Omit<InputProps, 'defaultValue'> {
  showFeedback?: boolean
}

export const FormTextInput: React.FC<IProps> = ({
  field,
  validate,
  showFeedback = false,
  ...inputProps
}) => (
  <Field
    field={field}
    validate={validate}
    render={fieldApi => (
      <>
        <Input
          {...inputProps}
          value={fieldApi.value}
          onChange={e => fieldApi.setValue(e.target.value)}
          onBlur={() => fieldApi.setTouched(true)}
          invalid={fieldApi.touched && Boolean(fieldApi.error)}
        />
        {showFeedback && <FormFeedback>{fieldApi.error}</FormFeedback>}
      </>
    )}
  />
)
