import './CategoryNavigator.css'

import { cn } from '@speedlo/ui'
import React, { useState } from 'react'
import Scroll from 'react-scroll'
import Truncate from 'react-truncate'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../../common/Responsive'
import { FFoodMenuCategoryFragment } from '../../graph/generated'

interface IProps {
  categories: ReadonlyArray<FFoodMenuCategoryFragment>
}

const StyledCategoryNavigator = styled.div`
  background-color: #d4d4d4;
  border-top-left-radius: 0.6rem;
  padding: 0.7rem 0.5rem 0 0.2rem;
  position: sticky;
  top: 0;
  z-index: 1;
  user-select: none;
  display: flex;
  ${mediaDesktop`
    flex-wrap: wrap;
  `}
  ${mediaMobile`
    flex-wrap: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    box-shadow: inset -15px -5px 15px -14px rgba(0, 0, 0, 0.6);
    margin: 0;
    border-top-left-radius: 0;
    background-color: #f3f3f3;
    opacity: 0.94;
  `}
`

const CategoryNavigator: React.FC<IProps> = ({ categories }) => {
  const [activeCategoryIndex, setActiveCategoryIndex] = useState(0)
  return (
    <StyledCategoryNavigator data-testid="CategoryNavigator">
      {categories.length === 0
        ? null
        : categories.map((category, idx) => (
            <Scroll.Link
              to={category.nameLabel}
              key={category.id}
              className={cn('category__navigator__scroll__link', {
                active: activeCategoryIndex === idx,
              })}
              onSetActive={() => setActiveCategoryIndex(idx)}
              offset={-168}
              smooth={true}
              spy={true}
              data-testid={`categoryNavigatorScrollLink__${category.id}`}
            >
              <div className="category__navigator__scroll__link__circle">
                <span className={`flaticon-${category.pictogram}`} />
              </div>
              <div className="category__navigator__scroll__link__description">
                <Truncate lines={2}>{category.nameLabel}</Truncate>
              </div>
            </Scroll.Link>
          ))}
    </StyledCategoryNavigator>
  )
}

export default CategoryNavigator
