import React from 'react'

import { PrimaryButton } from '../../common/Buttons'
import ButtonStripe from '../../common/ButtonStripe'
import { i18n } from '../../i18n'

interface IProps {
  enabled: boolean
  onClick(): void
  onPrefetch(): void
}

export const AddressSubmit: React.FC<IProps> = ({
  enabled,
  onClick,
  onPrefetch,
}) => (
  <ButtonStripe>
    <PrimaryButton
      color="primary"
      disabled={!enabled}
      onClick={onClick}
      onMouseEnter={() => {
        if (enabled) {
          onPrefetch()
        }
      }}
      data-testid="AddressSubmit"
    >
      {i18n.t`Confirm address`.toLocaleUpperCase()}
    </PrimaryButton>
  </ButtonStripe>
)

export default AddressSubmit
