import '../../common/Modal.css'

import { Trans } from '@lingui/macro'
import { isoDate } from '@speedlo/graphql'
import { RM } from '@speedlo/tools'
import { addMinutes, isAfter } from 'date-fns'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Container, Modal, ModalBody, ModalHeader } from 'reactstrap'

import { useRoot } from '../../app/useRoot'
import { PrimaryButton } from '../../common/Buttons'
import {
  useMOrderDeliveryTime,
  useQOrderTimePicker,
} from '../../graph/generated'
import { i18n } from '../../i18n'
import { TimePicker } from '../timePicker/TimePicker'

export const OrderTimePicker: React.FC = () => {
  const { order, cart } = useRoot()

  const { data, loading } = useQOrderTimePicker({ branchId: order.branchId! })

  const [updateOrder] = useMOrderDeliveryTime()

  const selectedTime = React.useRef<Date | undefined>(order.deliverAt)
  const onConfirmAndClose = () => {
    const time = selectedTime.current
    updateOrder({
      orderId: order.orderId!,
      deliverAt: time && isoDate(time),
      withGoods: cart.withGoods,
    })
    order.setDeliverAt(time)
    order.closeTimePicker()
  }

  const getStartTime = () => {
    const { companyBranch } = data
    const openAt =
      companyBranch.isOpen || !companyBranch.willOpenAt
        ? new Date()
        : new Date(companyBranch.willOpenAt)
    return openAt
  }

  const getEndTime = () => {
    const { companyBranch } = data
    if (companyBranch.isOpen && companyBranch.willCloseAt) {
      return new Date(companyBranch.willCloseAt)
    }
    const nextBusinessHour = companyBranch.businessHours.find(
      RM.propEq('closed', false),
    )
    return nextBusinessHour ? new Date(nextBusinessHour.closeAt) : null
  }

  const getFilterIntervals = (): Interval[] => {
    const { companyBranch } = data
    return companyBranch.businessHours
      .filter(RM.propEq('closed', true))
      .map(bhour => ({
        start: new Date(bhour.openAt),
        end: new Date(bhour.closeAt),
      }))
  }

  const getMinuteOffset = () => {
    const { defaultDeliveryTime, defaultPickupTime } = data.companyBranch

    if (order.usingDeliveryOrderMethod) {
      return defaultDeliveryTime
    }
    if (order.usingPickupOrderMethod) {
      return defaultPickupTime
    }

    return 0
  }

  const renderModalContent = () => {
    if (loading) {
      return null
    }

    const minDate = getStartTime()
    const maxDate = getEndTime()
    if (maxDate === null || isAfter(minDate, maxDate)) {
      return (
        <Trans>
          It's not possible to pick a specific order time at this moment.
        </Trans>
      )
    }

    const minuteOffset = getMinuteOffset()

    return (
      <TimePicker
        minuteStep={15}
        minDate={addMinutes(minDate, minuteOffset)}
        maxDate={addMinutes(maxDate, minuteOffset)}
        initialTime={selectedTime.current}
        filterIntervals={getFilterIntervals()}
        onStepChange={time => (selectedTime.current = time)}
      />
    )
  }

  React.useEffect(() => {
    if (
      data &&
      data.companyBranch &&
      data.companyBranch.delayedDeliveryEnabled
    ) {
      order.enableDelayedOrder()
    }
  }, [data])

  const header = order.usingDeliveryOrderMethod
    ? i18n.t`Deliver at what time?`
    : i18n.t`When you will pickup the order?`

  return useObserver(() => (
    <Modal
      isOpen={order.timePickerOpen}
      toggle={order.closeTimePicker}
      className="modal__container"
      autoFocus={true}
      backdrop="static"
      fade={false}
    >
      <ModalHeader
        toggle={order.closeTimePicker}
        tag={Container}
        className="modal__header"
      >
        <span>{header}</span>
      </ModalHeader>
      <ModalBody className="modal__body">
        <div className="modal__body__content">{renderModalContent()}</div>
        <div className="modal__body__button__line">
          <div className="modal__body__button__strip" />
          <PrimaryButton
            onClick={onConfirmAndClose}
            size="md"
            className="modal__body__button"
          >
            {i18n.t`Confirm time`.toLocaleUpperCase()}
          </PrimaryButton>
        </div>
      </ModalBody>
    </Modal>
  ))
}
