import { useApolloClient } from '@speedlo/graphql'
import { useRouterContext } from '@speedlo/hooks'
import { idPropEq, RM } from '@speedlo/tools'
import React from 'react'

import { useRoot } from '../../../app/useRoot'
import {
  MRemoveCartItemMutation,
  MRemoveCartItemVariables,
  QCartDocument,
  QCartQuery,
  QCartVariables,
  useMRemoveCartItem,
} from '../../../graph/generated'

export function useCartControl() {
  const { order: orderModel, cart: cartModel } = useRoot()
  const client = useApolloClient()
  const { history } = useRouterContext()
  const onItemEdit = React.useCallback((item: ID) => {
    orderModel.changeCartItem(item)
  }, [])

  const [removeItem] = useMRemoveCartItem()

  const onItemRemove = React.useCallback((item: ID) => {
    const variables: MRemoveCartItemVariables = {
      itemId: item,
      withGoods: cartModel.withGoods,
      withPresence: cartModel.withPresence,
    }
    removeItem(variables, {
      optimisticResponse: makeOptimisticRemove(variables),
      context: {
        noBatch: true,
      },
    })
  }, [])

  const onPlaceOrder = React.useCallback(() => {
    history.push('/confirm')
  }, [])

  const onChangeOrder = React.useCallback(() => {
    history.push('/order')
  }, [])

  return { cartModel, onItemEdit, onItemRemove, onPlaceOrder, onChangeOrder }

  function makeOptimisticRemove(
    variables: MRemoveCartItemVariables,
  ): MRemoveCartItemMutation | undefined {
    const current = client.readQuery<QCartQuery, QCartVariables>(
      {
        query: QCartDocument,
        variables: {
          orderId: orderModel.orderId!,
          withGoods: cartModel.withGoods,
          withPresence: cartModel.withPresence,
        },
      },
      true,
    )
    if (!current) {
      return undefined
    }
    const rejectItem = RM.reject(idPropEq(variables.itemId))
    return {
      __typename: 'Mutation',
      remove: {
        ...current.order,
        countWares: current.order.countWares - 1,
        totalSum: {
          ...current.order.totalSum,
          value: -1,
        },
        items: variables.withGoods
          ? rejectItem(current.order.items)
          : current.order.items,
        presence: variables.withPresence
          ? rejectItem(current.order.presence)
          : current.order.presence,
      },
    }
  }
}
