import React from 'react'
import { FormGroup } from 'reactstrap'

import { FormTextInput } from '../../common/FormTextInput'
import { i18n } from '../../i18n'

const emailRx = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i

export const OrderEmailInputValidator = value => {
  const email = value.trim()
  if (email === '') {
    return i18n.t`E-mail is mandatory`
  }
  if (!emailRx.test(email)) {
    return i18n.t`E-mail format is not valid`
  }
  return null
}

export const OrderEmailInput: React.FC = () => (
  <FormGroup>
    <FormTextInput
      field="email"
      type="email"
      autoComplete="email"
      placeholder="E-mail"
      showFeedback={true}
      validate={OrderEmailInputValidator}
      data-testid="orderEmailInput"
    />
  </FormGroup>
)
