import { autorun } from 'mobx'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Field } from 'react-form'
import FormGroup from 'reactstrap/lib/FormGroup'
import Input from 'reactstrap/lib/Input'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { useMOrderTipUpdate } from '../../graph/generated'
import { i18n } from '../../i18n'

const TipContainer = styled.div`
  margin-top: 0.8rem;
`

export const OrderFormTipInput = () => {
  const { order, cart } = useRoot()
  const [updateTip] = useMOrderTipUpdate()

  React.useEffect(
    () =>
      autorun(() => {
        const tips = order.usingPaymentMethodWithTips ? Number(order.tips) : 0
        updateTip({
          tips,
          orderId: order.orderId!,
          withGoods: cart.withGoods,
        })
      }),
    [],
  )

  return useObserver(() => {
    if (!order.usingPaymentMethodWithTips) {
      return null
    }
    return (
      <TipContainer>
        <FormGroup>
          <Field
            field="tips"
            render={fieldApi => (
              <Input
                type="select"
                value={fieldApi.value}
                onChange={e => {
                  const { value } = e.target
                  fieldApi.setValue(value)
                  order.setTips(value)
                }}
                onBlur={() => {
                  fieldApi.setTouched(true)
                }}
                data-testid="orderTipsSelect"
              >
                <option value="">{i18n.t`Without tips`}</option>
                <option value="5">{i18n.t`Tips 5%`}</option>
                <option value="10">{i18n.t`Tips 10%`}</option>
                <option value="15">{i18n.t`Tips 15%`}</option>
                <option value="20">{i18n.t`Tips 20%`}</option>
                <option value="25">{i18n.t`Tips 25%`}</option>
              </Input>
            )}
          />
        </FormGroup>
      </TipContainer>
    )
  })
}
