import './OrderMethod.css'

import { RM } from '@speedlo/tools'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { SelectButtonThemed } from '../../common/Buttons'
import { DeliveryTypeEnum } from '../../graph/generated'
import { i18n } from '../../i18n'
import { TOrderModel } from '../../models/OrderModel'

interface IProps {
  onChange?(order: TOrderModel): void
}

interface IButtonProps {
  orderModel: TOrderModel
  orderMethod: DeliveryTypeEnum
  onChange(order: TOrderModel): void
  onPrefetch?(): void
}

const OrderMethodButton: React.FC<IButtonProps> = ({
  orderModel,
  orderMethod,
  onChange,
  onPrefetch,
  children,
}) => {
  return useObserver(() => (
    <SelectButtonThemed
      selected={orderModel.orderMethod === orderMethod}
      onMouseEnter={onPrefetch}
      onClick={() => {
        orderModel.useOrderMethod(orderMethod)
        onChange(orderModel)
      }}
      data-testid={`orderMethod-${orderMethod}`}
      data-testselected={orderModel.orderMethod === orderMethod}
    >
      {children}
    </SelectButtonThemed>
  ))
}

export const OrderMethod: React.FC<IProps> = ({ onChange }) => {
  const { order } = useRoot()
  return (
    <div className="order__method">
      <OrderMethodButton
        orderModel={order}
        orderMethod={DeliveryTypeEnum.Messenger}
        onChange={onChange || RM.identity}
      >
        <span className="flaticon-transport" />
        {i18n.t`I want to deliver`.toLocaleUpperCase()}
      </OrderMethodButton>
      <OrderMethodButton
        orderModel={order}
        orderMethod={DeliveryTypeEnum.Pickup}
        onChange={onChange || RM.identity}
        onPrefetch={order.prefetchPickup}
      >
        <span className="flaticon-restaurant" />
        {i18n.t`I'll pick up`.toLocaleUpperCase()}
      </OrderMethodButton>
    </div>
  )
}
