import { Plural, Trans } from '@lingui/react'
import React from 'react'
import { Field } from 'react-form'
import { FormFeedback, FormGroup, FormText, Input } from 'reactstrap'

import { i18n } from '../../i18n'

export const MAX_LENGTH = 50
const isTooLong = value => value && value.trim().length > MAX_LENGTH

const OrderNoteInputValidator = value =>
  isTooLong(value) ? i18n.t`Note is too long` : null

export const OrderNoteInput: React.FC = () => {
  const ingredientsDescription = i18n.t`eg. ingredients change, company...`
  return (
    <Field
      field="note"
      validate={OrderNoteInputValidator}
      render={fieldApi => (
        <FormGroup>
          <Input
            type="textarea"
            placeholder={i18n.t`Note`}
            title={ingredientsDescription}
            value={fieldApi.value}
            onChange={e => fieldApi.setValue(e.target.value)}
            onBlur={() => fieldApi.setTouched(true)}
            valid={!fieldApi.error}
            invalid={Boolean(fieldApi.error)}
            data-testid="orderNoteInput"
          />
          <FormText>{ingredientsDescription}</FormText>
          {isTooLong(fieldApi.value) ? (
            <FormFeedback>
              <Trans>
                Maximum length is{' '}
                <Plural value={MAX_LENGTH} other="# characters" />. Typed{' '}
                <Plural
                  value={fieldApi.value.length}
                  one="# char"
                  other="# chars"
                />
              </Trans>
            </FormFeedback>
          ) : null}
        </FormGroup>
      )}
    />
  )
}
