import {
  addMinutes,
  endOfMinute,
  getMinutes,
  startOfMinute,
  subMinutes,
} from 'date-fns'

export function createTimeSteps(start: Date, end: Date, minuteStep: number) {
  let current = startOfMinute(timeClampUp(start, minuteStep))
  const steps: Date[] = []
  const endMinute = endOfMinute(subMinutes(end, 1))
  do {
    steps.push(current)
    current = addMinutes(current, minuteStep)
  } while (current <= endMinute)
  return steps
}

export function timeClampUp(time: Date, minuteStep: number) {
  return addMinutes(timeClampDown(time, minuteStep), minuteStep)
}

export function timeClampDown(time: Date, minuteStep: number) {
  return subMinutes(time, getMinutes(time) % minuteStep)
}
