import React from 'react'
import { Field } from 'react-form'

import { useRoot } from '../../app/useRoot'
import { SelectButtonThemed } from '../../common/Buttons'
import { PaymentGateEnum } from '../../graph/types'

export interface IPaymentButtonProps {
  method: PaymentGateEnum
  disabled?: boolean
}

export const OrderPaymentButton: React.FC<IPaymentButtonProps> = ({
  children,
  disabled,
  method,
}) => {
  const { order: orderModel } = useRoot()
  return (
    <Field
      field="payment"
      render={fieldApi => (
        <SelectButtonThemed
          disabled={disabled}
          selected={fieldApi.value === method}
          onClick={() => {
            fieldApi.setValue(method)
            orderModel.setPaymentMethod(method)
          }}
          data-testid={`orderPaymentMethod-${method}`}
        >
          <Checkmark show={fieldApi.value === method} />
          {children}
        </SelectButtonThemed>
      )}
    />
  )
}

function Checkmark({ show }: { show: boolean }) {
  return show ? (
    <span className="flaticon-mark order__payment__method__button__checked" />
  ) : (
    <div className="order__payment__method__button__unchecked" />
  )
}
