import React from 'react'
import { FormGroup, InputGroup } from 'reactstrap'
import { UAParser } from 'ua-parser-js'

import { FormTextInput } from '../../common/FormTextInput'
import { i18n } from '../../i18n'

export const OrderPhoneInputValidator = value =>
  value.trim() === '' ? i18n.t`Phone is mandatory` : null

export const OrderPhonePrefixInputValidator = value =>
  value.trim() === '' ? i18n.t`Phone prefix is mandatory` : null

export const OrderPhoneInput: React.FC = () => {
  const agent = new UAParser(window.navigator.userAgent)
  const isMobileSafari = agent.getBrowser().name === 'Mobile Safari'
  return (
    <FormGroup>
      <InputGroup>
        <FormTextInput
          field="phonePrefix"
          autoComplete={isMobileSafari ? 'off' : 'tel-country-code'}
          placeholder="+"
          validate={OrderPhonePrefixInputValidator}
        />
        <FormTextInput
          field="phone"
          type="tel"
          autoComplete="tel-national"
          placeholder={i18n.t`Phone`}
          validate={OrderPhoneInputValidator}
          style={{ width: '55%' }}
          data-testid="orderPhoneInput__Phone"
        />
      </InputGroup>
    </FormGroup>
  )
}
