import { Plural, Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { AlertModal } from '../../common/AlertModal'
import { FCompanyBranchStatusFragment } from '../../graph/generated'
import { useShopStatus } from '../../hooks/useShopStatus'
import { i18n } from '../../i18n'

interface IProps {
  status: FCompanyBranchStatusFragment
}

export const ShopClosingTimeModal: React.FC<IProps> = ({ status }) => {
  const { user } = useRoot()
  const { decision, openingIn } = useShopStatus({
    status,
    // Do not want update here as it would be showing modal every time :)
    updateInterval: 0,
  })
  return useObserver(() => (
    <AlertModal
      isOpen={
        (decision === 'Closed' || decision === 'OpeningSoon') &&
        !user.closingTimeAlertHidden
      }
      onClose={user.hideClosingTime}
      headerText={i18n.t`We are closed right now`}
    >
      {i18n.t`We're sorry, but we do not accept new orders at this time.`}
      {decision === 'OpeningSoon' && (
        <Trans>
          You can prepare your order because we are{' '}
          <Plural
            value={openingIn}
            one="opening in 1 minute"
            other="opening in # minutes"
          />
          !
        </Trans>
      )}
    </AlertModal>
  ))
}
