import './OrderPaymentMethod.css'

import { DateFormat, Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { SelectButtonThemed } from '../../common/Buttons'

export const OrderTimeButtons: React.FC = () => {
  const { order } = useRoot()

  return useObserver(() => {
    if (!order.delayedOrderEnabled) {
      return null
    }
    return (
      <div className="order__payment__method">
        <SelectButtonThemed
          data-testid="orderTimeButtonNow"
          selected={order.deliverAt === undefined}
          onClick={() => order.setDeliverAt(undefined)}
        >
          <Trans>When possible</Trans>
        </SelectButtonThemed>
        <SelectButtonThemed
          data-testid="orderTimeButtonLater"
          selected={order.deliverAt !== undefined}
          onClick={order.openTimePicker}
        >
          {order.deliverAt ? (
            <DateFormat
              value={order.deliverAt}
              format={{ weekday: 'long', hour: 'numeric', minute: 'numeric' }}
            />
          ) : (
            <Trans>Choose time</Trans>
          )}
        </SelectButtonThemed>
      </div>
    )
  })
}
