import { useOrderPromoCodes } from '@speedlo/ui'
import React from 'react'
import { FormGroup, Input, InputGroup } from 'reactstrap'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { SecondaryButton } from '../../common/Buttons'
import {
  useMOrderPromoAdd,
  useMOrderPromoRemove,
  useQOrderPromoCodes,
} from '../../graph/generated'
import { useAlertModal } from '../../hooks/useAlertModal'
import { i18n } from '../../i18n'

const CodesPanel = styled.div`
  margin-top: 0.5rem;
  & button {
    margin-right: 0.5rem;
    font-size: 0.7rem;
  }
`

export const OrderPromoInput = React.memo(() => {
  const { order, cart } = useRoot()
  const [errorModal, showError] = useAlertModal()

  const { data, loading } = useQOrderPromoCodes(
    { orderId: order.orderId! },
    { skip: !order.orderId },
  )
  const [addCodeMutation] = useMOrderPromoAdd()
  const [removeCodeMutation] = useMOrderPromoRemove()

  const onCodeAdd = React.useCallback(async (code: string) => {
    if (!order.orderId) {
      return
    }
    return addCodeMutation({
      code,
      orderId: order.orderId,
      withGoods: cart.withGoods,
    })
  }, [])

  const onCodeRemove = async (codeId: ID) => {
    if (order.orderId) {
      removeCodeMutation({
        codeId,
        orderId: order.orderId,
        withGoods: cart.withGoods,
      })
    }
  }

  const onCodeErrors = (code, errors) => {
    showError(
      i18n.t`Promo code "${code}" is invalid`,
      <div>
        {errors.map(msg => (
          <div key={msg}>{msg}</div>
        ))}
      </div>,
    )
  }

  const { inputProps, inputRef } = useOrderPromoCodes({
    onCodeAdd,
    onCodeErrors,
    getValidatingMessage: code => i18n.t`Validating promo code ${code}...`,
  })

  const codes = loading ? [] : data.order.promoCodes

  return (
    <FormGroup>
      {errorModal}
      <InputGroup>
        <Input
          innerRef={inputRef}
          autoComplete="off"
          placeholder={i18n.t`Promo codes`}
          {...inputProps}
        />
      </InputGroup>
      <CodesPanel data-testid="promoCodesPanel">
        {codes.map(({ id, code }) => (
          <SecondaryButton
            key={id}
            role="button"
            onClick={() => onCodeRemove(id)}
          >
            {`${code} `}
          </SecondaryButton>
        ))}
      </CodesPanel>
    </FormGroup>
  )
})
