import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { useQFoodMenu } from '../../graph/generated'
import CategoryNavigator from './CategoryNavigator'
import FoodMenu from './FoodMenu'
import { OrderLimitModal } from './OrderLimitModal'

export const FoodMenuView = observer(() => {
  const { order: orderModel } = useRoot()

  const { data, loading } = useQFoodMenu(
    {
      branchId: orderModel.branchId!,
    },
    {
      skip: !orderModel.branchId,
      fetchPolicy: 'cache-first',
    },
  )

  const categories = React.useMemo(
    () =>
      loading ? [] : data.wareCategories.filter(cat => cat.recipes.length > 0),
    [loading, data],
  )

  return (
    <>
      <OrderLimitModal />
      <CategoryNavigator categories={categories} />
      <FoodMenu
        orderId={orderModel.orderId}
        categories={categories}
        isLoading={loading}
      />
    </>
  )
})
