import React from 'react'
import { Field, FieldType } from 'react-form'
import { Input } from 'reactstrap'
import styled from 'styled-components/macro'

interface IProps {
  field: FieldType
  defaultValue?: boolean
  validate?(value: boolean): string | null
}

const CheckboxContainerStyled = styled.div`
  display: flex;
  margin-bottom: 0.5rem;
`

const CheckboxStyled = styled(Input)`
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: white;
  border: 1px solid;
  border-color: ${({ invalid }: { invalid: boolean }) =>
    invalid ? 'red' : '#c5c5c5'};
  width: 2rem;
  height: 2rem;
  min-width: 2rem;
  border-radius: 0.25rem;
  position: relative;
  margin: 0;
  outline: none;
  &:checked {
    background-color: white;
  }
  &:checked:after {
    content: '\\2714';
    font-size: 2rem;
    position: absolute;
    top: -0.5rem;
    left: 0.1rem;
    color: #1d1d1d;
    overflow: hidden;
  }
`

const LabelStyled = styled.div`
  margin: 0 0 0 0.8rem;
  vertical-align: top;
`

export const FormInputCheckbox: React.FC<IProps> = ({
  field,
  defaultValue = false,
  validate,
  children,
  ...props
}) => (
  <Field field={field} defaultValue={defaultValue} validate={validate}>
    {fieldApi => (
      <CheckboxContainerStyled>
        <CheckboxStyled
          type="checkbox"
          value={fieldApi.value}
          onChange={checked => fieldApi.setValue(checked.target.checked)}
          invalid={fieldApi.touched && Boolean(fieldApi.error)}
          {...props}
        />
        <LabelStyled>{children}</LabelStyled>
      </CheckboxContainerStyled>
    )}
  </Field>
)
