import { LanguagePicker as LangPick } from '@speedlo/ui'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../app/useRoot'

interface IProps {
  className?: string
}

const StyledLanguagePicker = styled(LangPick)`
  display: inline-block;

  & .arrow {
    order: 0 !important;
  }
`

export const LanguagePicker = observer<IProps>(({ className }) => {
  const { business, user } = useRoot()
  if (!business.isValid) {
    return null
  }
  return (
    <StyledLanguagePicker
      availableLanguages={business.branch!.supportedLanguages}
      selectedLanguage={user.language}
      onChange={language => {
        if (language !== user.language) {
          user.selectLanguage(language)
          window.location.reload()
        }
      }}
      className={className}
      backgroundColor="white"
      arrowColor="#5A5A5A"
    />
  )
})
