import './OrderSubmitLine.css'

import { cn } from '@speedlo/ui'
import React from 'react'

import { PrimaryButton } from '../../common/Buttons'

interface IProps {
  onClick(): void
  canSubmit: boolean
  isSubmitting: boolean
  children: ReactNode
}

export const OrderSubmitLine: React.FC<IProps> = ({
  onClick,
  canSubmit = false,
  isSubmitting,
  children,
}) => {
  const className = cn(
    'order__form__submit__line__button',
    'animated',
    'infinite',
    { heartBeat: isSubmitting },
  )
  return (
    <div className="order__form__submit__line">
      <div className="order__form__submit__line__strip" />
      <PrimaryButton
        className={className}
        color="primary"
        size="md"
        onClick={onClick}
        disabled={!canSubmit || isSubmitting}
        data-testid="orderSubmitButton"
      >
        {children}
      </PrimaryButton>
    </div>
  )
}
