import { autorun } from 'mobx'
import { useDisposable } from 'mobx-react-lite'
import React from 'react'

import { GeoPoint } from '../../../helpers/gps'
import { TMapModel } from '../../../models/MapModel'

interface IProps {
  gps?: Maybe<GeoPoint>
  map: TMapModel
}

export const MapPanning: React.FC<IProps> = ({ map, gps }) => {
  useDisposable(
    () =>
      autorun(() => {
        if (map.isReady && gps) {
          map.panToLocation(gps)
        }
      }),
    [gps],
  )
  return null
}
