import { fakerCZ } from '@speedlo/testing/dist/faker'
import React from 'react'

import { SecondaryButton } from '../../common/Buttons'
import { PaymentGateEnum } from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'
import { IOrderFormState } from './OrderFormSetup'
import { MAX_LENGTH } from './OrderNoteInput'

interface IProps {
  onFill(values: IOrderFormState): void
}

export const OrderFormAutoFill: React.FC<IProps> = ({ onFill }) => {
  const orderModel = useOrderModel()
  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }
  return (
    <SecondaryButton
      style={{ marginBottom: '0.5rem' }}
      onClick={() => {
        onFill({
          firstname: fakerCZ.name.firstName(),
          lastname: fakerCZ.name.lastName(),
          email: fakerCZ.internet.email(),
          phone: fakerCZ.phone.phoneNumberFormat(0).replace(/\s/g, ''),
          phonePrefix: '+420',
          note: fakerCZ.lorem.sentence().slice(0, MAX_LENGTH - 1),
          tips: '',
          payment: orderModel.payment as PaymentGateEnum,
          agreements: [],
        })
      }}
    >
      Autofill form [DEV ONLY]
    </SecondaryButton>
  )
}
