import { cn } from '@speedlo/ui'
import debounce from 'debounce'
import React, { Component } from 'react'
import { Input, InputProps } from 'reactstrap'

interface IProps {
  onSearch(search: string): void
  isVisible: boolean
  isSearching: boolean
  inputProps: InputProps
}

interface IState {
  value: string
}

class SearchInput extends Component<IProps, IState> {
  state = { value: '' }
  componentDidMount() {
    this.setFocus()
  }
  componentDidUpdate(nextProps: IProps) {
    if (nextProps.isVisible !== this.props.isVisible) {
      this.setFocus()
    }
  }
  onSearch = debounce(this.props.onSearch, 2000)
  onChange = ev => {
    const value = ev.target.value || ''
    this.setState({ value })
    this.onSearch(value.trim())
  }
  onKeyPress = ev => {
    if (ev.key === 'Enter') {
      this.onSearch.clear()
      this.props.onSearch(this.state.value.trim())
    }
  }
  inputRef: Maybe<HTMLInputElement> = null
  onInputMount = (ref: HTMLInputElement) => {
    this.inputRef = ref
  }
  isVisible() {
    return this.props.isVisible !== false
  }
  isSearching() {
    return this.props.isSearching !== false
  }
  setFocus() {
    if (this.isVisible() && this.inputRef !== null) {
      this.inputRef.focus()
    }
  }
  render() {
    const { inputProps } = this.props
    const isHidden = !this.isVisible() || this.isSearching()
    return (
      <Input
        className={cn({ address__input__hidden: isHidden })}
        onChange={this.onChange}
        onKeyPress={this.onKeyPress}
        value={this.state.value}
        innerRef={this.onInputMount}
        autoFocus={true}
        data-testid="SearchInput"
        {...inputProps}
      />
    )
  }
}

export default SearchInput
