import React from 'react'
import Spinner from 'react-spinkit'

interface IProps {
  address: string
  isVisible: boolean
  isResolving: boolean
  onValidClick(): void
}

export const ResolvedAddress: React.FC<IProps> = ({
  address,
  isVisible,
  isResolving,
  onValidClick,
}) => {
  if (isResolving) {
    return (
      <div className="address__input__box__resolved wait">
        <Spinner name="ball-beat" fadeIn="none" />
      </div>
    )
  }
  if (!isVisible) {
    return null
  }
  return (
    <div className="address__input__box__resolved" onClick={onValidClick}>
      {address}
    </div>
  )
}

export default ResolvedAddress
