import React from 'react'
import { FormGroup, InputGroup } from 'reactstrap'

import { FormTextInput } from '../../common/FormTextInput'
import { i18n } from '../../i18n'

export const OrderNameInput: React.FC = () => (
  <FormGroup>
    <InputGroup>
      <FormTextInput
        field="firstname"
        autoComplete="given-name"
        placeholder={i18n.t`First name`}
        validate={value =>
          value.trim() === '' ? i18n.t`First name is mandatory` : null
        }
        data-testid="orderNameInput__FirstName"
      />
      <FormTextInput
        field="lastname"
        autoComplete="family-name"
        placeholder={i18n.t`Last name`}
        validate={value =>
          value.trim() === '' ? i18n.t`Last name is mandatory` : null
        }
        data-testid="orderNameInput__LastName"
      />
    </InputGroup>
  </FormGroup>
)
