import { observable } from 'mobx'
import { useObserver } from 'mobx-react-lite'
import React, { useCallback, useRef } from 'react'
import { Button, Collapse } from 'reactstrap'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { FormInputCheckbox } from '../../common/FormInputCheckbox'
import { i18n } from '../../i18n'
import { TAgreement } from '../../models/OrderModel'

const ButtonStyled = styled(Button)`
  padding: 0 0.5rem;
  font-size: 0.8rem;
  color: darkgray;
`

const GdprAgreementStyled = styled.div`
  font-size: 0.8rem !important;
  color: darkgray;
  margin: 0.5rem 0 0.5rem 0;
  & .row {
    margin: 0;
  }
`

const hideConfirmed = (agreement: TAgreement) => {
  if (agreement.isConfirmed) {
    // it's necessary to hide confirmed agreements like this
    // because react-form already has validation errors stored
    // for mandatory ones and is unable to forget it by not rendering field
    return { style: { display: 'none' } }
  }
  return {}
}

export const OrderAgreements: React.FC = () => {
  const descOpen = useRef(observable.array<ID>([])).current
  const toggleId = useCallback(
    (id: ID) => () => {
      if (descOpen.includes(id)) {
        descOpen.remove(id)
      } else {
        descOpen.push(id)
      }
    },
    [],
  )
  const { order } = useRoot()
  return useObserver(() => (
    <>
      {order.agreements.map((agreement, idx) => (
        <div key={agreement.id} {...hideConfirmed(agreement)}>
          <FormInputCheckbox
            field={['agreements', idx, 'isConfirmed']}
            validate={checked =>
              agreement.isMandatory && !(agreement.isConfirmed || checked)
                ? 'Agreement is mandatory'
                : null
            }
            data-testid={`orderAgreement__Check__${agreement.id}`}
          >
            <span dangerouslySetInnerHTML={{ __html: agreement.name }} />
            {agreement.isMandatory && ' * '}
            {agreement.description && (
              <>
                <ButtonStyled
                  color="link"
                  onClick={toggleId(agreement.id)}
                  data-testid={`orderAgreement__MoreButton__${agreement.id}`}
                >
                  {descOpen.includes(agreement.id)
                    ? i18n.t`Less`.toLocaleLowerCase()
                    : i18n.t`More...`.toLocaleLowerCase()}
                </ButtonStyled>
                <Collapse isOpen={descOpen.includes(agreement.id)}>
                  <GdprAgreementStyled
                    data-testid={`orderAgreement__Info__${agreement.id}`}
                    dangerouslySetInnerHTML={{
                      __html: agreement.description,
                    }}
                  />
                </Collapse>
              </>
            )}
          </FormInputCheckbox>
        </div>
      ))}
    </>
  ))
}
