import './DeliveryMap.css'

import { Marker } from '@react-google-maps/api'
import { when } from 'mobx'
import { useDisposable, useObserver } from 'mobx-react-lite'
import React, { useCallback } from 'react'

import { useRoot } from '../../app/useRoot'
import { latLngToGps } from '../../helpers/gps'
import { AddressInput } from '../destination/AddressInput'
import { AddressSubmit } from './AddressSubmit'
import { DeliveryMapPanning } from './DeliveryMapPanning'

interface IProps {
  onConfirm(): void
}

export const DeliveryMap: React.FC<IProps> = ({ onConfirm }) => {
  const { business, order, location } = useRoot()

  const onSubmit = useCallback(async () => {
    if (location.branchId) {
      order.setBranch(location.branchId)
    }
    await order.createDeliveryOrder()
    onConfirm()
  }, [onConfirm])

  useDisposable(() =>
    when(() => business.isValid, location.validateUserLocation),
  )

  return useObserver(() => (
    <>
      <DeliveryMapPanning />
      {location.latLng ? (
        <Marker
          position={location.latLng}
          draggable={true}
          onDragEnd={({ latLng }) => {
            location.withUserLocation(latLngToGps(latLng))
          }}
        />
      ) : null}
      <div className="delivery__map__address">
        <AddressInput />
      </div>
      <div className="delivery__map__submit">
        <AddressSubmit
          enabled={location.isDeliveryAddress}
          onClick={onSubmit}
          onPrefetch={() => {
            if (location.branchId) {
              order.prefetchFoodMenu(location.branchId)
            }
          }}
        />
      </div>
    </>
  ))
}
