import { GoogleMap, LoadScript } from '@react-google-maps/api'
import { Observer } from 'mobx-react-lite'
import React from 'react'

import { appConfig } from '../config'
import { TMapModel } from '../models/MapModel'

interface IBaseMapProps {
  model: TMapModel
}

const getMapOptions = (): google.maps.MapOptions => {
  return {
    streetViewControl: false,
    mapTypeControl: false,
    fullscreenControl: false,
    zoomControlOptions: {
      position: google.maps.ControlPosition.RIGHT_TOP,
    },
  }
}

const libraries = ['places']

export const BaseMap: React.FC<IBaseMapProps> = ({ model, children }) => {
  const [isLoaded, setLoaded] = React.useState(false)

  const renderMap = () => (
    <Observer>
      {() => (
        <GoogleMap
          zoom={model.zoom}
          center={model.center}
          onCenterChanged={model.updatePosition}
          onZoomChanged={model.updateZoom}
          onClick={model.onClick}
          onLoad={model.setMapRef}
          options={getMapOptions()}
          mapContainerStyle={{
            borderRadius: '0 0 0.5rem 0.5rem',
            overflow: 'hidden',
            height: 'calc(100% - 3.75rem)',
          }}
        >
          {children}
        </GoogleMap>
      )}
    </Observer>
  )

  return (
    <LoadScript
      id="script-loader"
      googleMapsApiKey={appConfig.googleMapsApi!}
      libraries={libraries}
      version="weekly"
      onLoad={() => setLoaded(true)}
    >
      {isLoaded ? renderMap() : null}
    </LoadScript>
  )
}
