import React from 'react'
import { InputGroupAddon } from 'reactstrap'
import styled from 'styled-components/macro'

interface IProps {
  isVisible: boolean
  onClick(): void
}

const Container = styled.span`
  color: ${({ theme }) => theme.secondary};
  cursor: pointer;
  position: absolute;
  right: 0.4rem;
  top: 0.15rem;
`

export const GeolocationButton: React.FC<IProps> = ({
  onClick,
  isVisible = false,
}) =>
  isVisible ? (
    <Container>
      <InputGroupAddon addonType="append" onClick={onClick}>
        <span className="flaticon-gps" />
      </InputGroupAddon>
    </Container>
  ) : null

export default GeolocationButton
